import React, { useState, useEffect } from "react";
import {createWidget} from "@utorgapp/widget";
import { DEV_ADJUST_TRACKERS } from "../../constants";

import './Widget.css'

export const Widget = ({controlledOptions, isDevMode}) => {
  const [widget, setWidget] = useState('');

  useEffect(() => {
    if (Object.keys(controlledOptions).length) {
      let attributes = {};
      if (isDevMode) {
        attributes.adj_t = DEV_ADJUST_TRACKERS;
      }
      createWidget({ options: controlledOptions, attributes }).then(w => {
        setWidget(w)
      }).catch(e => {
        console.error('widget error', e);
      })
    }
  }, [controlledOptions, isDevMode])

  return (
    <div dangerouslySetInnerHTML={{ __html: widget }}></div>
  )
}
