import React, { useState } from 'react';
import { Col, FlexboxGrid } from 'rsuite';

import './App.css';
import { Widget, WidgetForm } from './components';
import { IS_INTERNAL } from './constants'

function App() {
  const [options, setOptions] = React.useState({})
  const [isDevMode, setIsDevMode] = useState(IS_INTERNAL)

  return (
    <FlexboxGrid justify="center" align="middle">
      <FlexboxGrid.Item as={Col} className="widget__col">
        <WidgetForm
          onSubmit={(data) => setOptions(data)}
          isDevMode={isDevMode}
          setIsDevMode={setIsDevMode}
        />
      </FlexboxGrid.Item>
      <FlexboxGrid.Item as={Col} className="widget__col">
        <Widget controlledOptions={options} isDevMode={isDevMode} />
      </FlexboxGrid.Item>
    </FlexboxGrid>
  );
}

export default App;
