import { InitOptions } from './types'
import { BASE_TEMPLATE_URL, ENV, QR_CODE_PATH } from './constants'

export * from './types'
export * from './utils'

const defaultOptions: InitOptions = {
	baseUrl: BASE_TEMPLATE_URL,
	path: QR_CODE_PATH,
}

export const init = (options: InitOptions = defaultOptions) => {
	ENV.baseUrl = options.baseUrl
	ENV.path = options.path
}
